.map {
  width: 100%;
}
.map-h2 {
  font-size: 1rem;
  padding: 20px;
  padding-left: 10px;
  text-align: center;
}
.google-map {
  height: 60vh;
}

.pin {
  display: flex;
  align-items: center;
  width: 180px;
  color: var(--main-blue);
}
.pin-icon {
  font-size: 4rem;
}
.pin-text {
  font-size: 1.3em;
}

@media screen and (min-width: 799px) {
  .google-map {
    height: 60vh;
  }
  .map-h2 {
    font-size: 1.3rem;
    font-weight: 400;
  }
  .pin {
    width: 15vw;
  }
  .pin-icon {
    font-size: 10vw;
  }
}
